import Link from 'next/link'

const projects = [
  { slug: 'project-alpha', title: 'Project Alpha', short: 'A modern platform UI.' },
  { slug: 'project-beta', title: 'Project Beta', short: 'Ecommerce redesign.' },
  { slug: 'project-gamma', title: 'Project Gamma', short: 'Marketing site for a SaaS.' },
]

export default function Projects() {
  return (
    <main className="py-20">
      <div className="container mx-auto">
        <h1 className="text-4xl font-bold mb-6">Projects</h1>

        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          {projects.map(p => (
            <Link
              key={p.slug}
              href={`/projects/${p.slug}`}
              className="block rounded-lg overflow-hidden shadow hover:scale-105 transition"
            >
              <div className="h-44 bg-gradient-to-br from-indigo-500 to-sky-500 flex items-center justify-center text-white font-bold">
                {p.title}
              </div>
              <div className="p-4 bg-white">
                <p className="text-gray-700">{p.short}</p>
              </div>
            </Link>
          ))}
        </div>
      </div>
    </main>
  )
}
